.scrollup {
    position: fixed;
    bottom: -20%;
    right: 2.5rem;
    background-color: var(--title-color);
    opacity: 0.8;
    padding: .3rem .5rem;
    border-radius: 0.4rem;
    z-index: var(--z-tooltip);
    transition: .4s;
}

.scrollup__icon {
    font-size: 1.5rem;
    color: var(--container-color);
}

.scrollup:hover {
    background-color: var(--title-color-dark);
}

/* Show Scroll */
.show-scroll {
    bottom: 3rem;
}

/*========== BREAKPOINTS ==========*/
/* For large devices */
@media screen and (min-width: 992px) {
    .scrollup {
        right: 1.5rem;
        padding: .25rem .4rem;
    }

    .scrollup__icon {
        font-size: 1.25rem;
    }
}

/* For medium devices */
@media screen and (max-width: 768px) {
    .show-scroll {
        bottom: 4rem;
    }
}


/* For small devices */
@media screen and (max-width: 350px) {
    .scrollup {
        right: 1rem;
    }
}
  